                    <view
                      class="btn-red"
                      @tap="openTonggao(item.basic.oid)"
                      v-if="item.status.publish_status == 120"
                      >开放招募</view
                    >
                    <view
                      class="btn-red"
                      @tap="refreshTonggao(item.basic.oid)"
                      v-if="
                        item.status.publish_status == 200 ||
                        item.status.publish_status == 700
                      "
                      >刷新排名</view
                    >
                    <view
                      class="btn-red"
                      @tap="reopenTonggao(item.basic.oid)"
                      v-if="item.status.publish_status == 300"
                      >重新打开</view
                    >
                    <view
                      class="btn-red"
                      @tap="manageTonggao(item.basic.oid)"
                      v-if="
                        item.status.publish_status == 200 ||
                        item.status.publish_status == 300
                      "
                      >管理报名</view
                    >
                    <view class="list_tips">
                      <image
                        class="warn_icon"
                        src="https://yuepai-oss.qubeitech.com/static/images/common/warn_icon.png"
                        v-if="item.audit.audit_reason"
                      ></image>
                      {{ item.audit.audit_reason }}
                    </view>
                  </view>
                </view>
              </view>
            </block>
            <view v-else class="none-data">
              <image
                src="https://yuepai-oss.qubeitech.com/static/images/common/none.png"
                mode="aspectFill"
                class="none-img"
              ></image>
              <view>当前暂无信息哦～</view>
            </view>
          </scroll-view>
        </swiper-item>
      </swiper>
    </view>
  </view>
</template>

<script>
import "./index.scss";
import { manageList, manageOperation } from "../../../api/index";
import { errortip, openPage } from "../../../utils/util";
export default {
  name: "tonggao_manage",
  data() {
    return {
      optionMap: {
        NE: "约拍",
        NT: "通告",
        PH: "作品",
      },
      currentTab: 0,
      // 页面配置
      winWidth: 0,
      winHeight: 0,
      list: [],
      status: 0,
      pageNum: 1,
      pageSize: 10,
    };
  },
  methods: {
    formatSex(sex) {
      if (sex == 1) {
        return "男";
      }
      if (sex == 0) {
        return "女";
      }
      return "不限";
    },
    // 点击tab切换
    changeItem(index, type) {
      if (this.currentTab === index) {
        return false;
      } else {
        this.currentTab = index;
        this.status = type;
        this.pageNum = 1;
        this.list = [];
        this.query();
      }
    },
    bindChange(e) {
      this.currentTab = e.detail.current;
    },
    scrollToLower() {
      this.pageNum++;
      this.query();
    },
    deleteTonggao(oid) {
      let _this = this;
      wx.showModal({
        title: "温馨提示",
        content: "确定删除该通告吗？",
        success: function (res) {
          if (res.confirm) {
            console.log("用户点击确定");
            _this.manageOperation({
              oid: oid,
              even_type: -200,
            });
          } else if (res.cancel) {
            console.log("用户点击取消");
          }
        },
      });
    },
    openTonggao(oid) {
      openPage("/packageTonggao/pages/open_recruitment/index?oid=" + oid);
    },
    overTonggao(oid) {
      let _this = this;
      wx.showModal({
        title: "温馨提示",
        content: "确定结束该通告，不再接收报名么？",
        success: function (res) {
          if (res.confirm) {
            console.log("用户点击确定");
            _this.manageOperation({
              oid: oid,
              even_type: 300,
            });
          } else if (res.cancel) {
            console.log("用户点击取消");
          }
        },
      });
    },
    refreshTonggao(oid) {
      let _this = this;
      wx.showModal({
        title: "温馨提示",
        content: "刷新当前通告需消耗5金币，确定刷新吗？",
        success: function (res) {
          if (res.confirm) {
            console.log("用户点击确定");
            _this.manageOperation({
              oid: oid,
              even_type: 400,
            });
          } else if (res.cancel) {
            console.log("用户点击取消");
          }
        },
      });
    },
    manageTonggao(oid) {
      openPage("/packageMsg/pages/manageSignup/index?oid=" + oid);
    },
    reopenTonggao(oid) {
      let _this = this;
      wx.showModal({
        title: "温馨提示",
        content: "确定重新打开该通告吗？",
        success: function (res) {
          if (res.confirm) {
            console.log("用户点击确定");
            _this.manageOperation({
              oid: oid,
              even_type: 200,
            });
          } else if (res.cancel) {
            console.log("用户点击取消");
          }
        },
      });
    },
    query() {
      let params = {
        type: "NT",
        publish_status: this.status,
        page: this.pageNum,
        per_page: this.pageSize,
      };
      this.manageList(params);
    },
    async manageList(params) {
      try {
        let res = await manageList(params);
        this.loading = true;
        if (!res.data.data || !res.data.data.items.length) {
          errortip("没有更多数据了～");
          return false;
        }
        let data = res.data.data.items;
        this.list = this.list.concat(data);
      } catch (error) {}
    },
    async manageOperation(params) {
      try {
        let res = await manageOperation(params);
        this.pageNum = 1;
        this.list = [];
        this.query();
      } catch (error) {}
    },
  },
  onShow() {
    let pages = getCurrentPages();
    let currPage = pages[pages.length - 1]; //当前页面
    if (currPage.data.refresh) {
      this.pageNum = 1;
      this.list = [];
      this.query();
    }
  },
  onLoad: function (options) {
    var that = this;
    // 获取系统信息
    wx.getSystemInfo({
      success: function (res) {
        that.winWidth = res.windowWidth;
        that.winHeight = res.windowHeight;
      },
    });
    this.query();
  },
};
</script>

<style lang="scss" scoped></style>
